#Checkout{
    width: 100%;
    height: 100%;
    -webkit-animation: FadeIn 1.5s;
            animation: FadeIn 1.5s;
    background-color: transparent;
    position: relative;
    padding: 7rem 0rem 0 2.5rem;
    position: relative;
}
@media all and (min-width: 1024px){
    #Checkout{
        padding: 7rem 0 0 15%;
    }
}
#Checkout>p.Goback{
    position: absolute;
    top: 3rem;
    left: 2.5rem;
    font-weight: 600;
}
#Checkout>p.Goback>i{
    margin-right: 1.5rem;
}
#Checkout>button.Goback{
    display: none;
}
@media all and (min-width: 1024px){
    #Checkout>p.Goback{
        display: none;
    }
    #Checkout>button.Goback{
        width: 4rem;
        display: inline-block;
        text-align: center;
        position: absolute;
        left: calc(15% - 5rem);
    }
}
#Checkout-Background{
    position: absolute;
    bottom: 0;
    left: 0;
    z-index: -1;
    background: -webkit-linear-gradient(var(--color-blue-normal), var(--color-blue-light));
    background: linear-gradient(var(--color-blue-normal), var(--color-blue-light));
    width: 100%;
    height: 50%;
}
#Checkout>p.Title{
    font-size: var(--font-size-extra);
    text-align: left;
    margin-bottom: 1rem;
}
#Checkout>div.Half-Card{
    width: 19.2rem;
    height: 6rem;
    overflow: hidden;
    margin-bottom: 1.5rem;
}
#Checkout>div.Half-Card>img{
    width:  100%;
    height: auto;
    margin: 0;
}
#Checkout>p.Tooltip-1{
    text-align: left;
    font-size: var(--font-size-small);
}
#Checkout>p.Tooltip-2{
    text-align: left;
    font-size: var(--font-size-large);
}
#Checkout>p.Tooltip-2>span{
    margin-left: 0.5rem;
    color: var(--color-grey-normal);
}
#Checkout>div.CryptoCard-Content{
    margin-top: 2.25rem;
    white-space: nowrap;
    overflow: auto hidden;
    margin-bottom: 0.5rem;
}
#Checkout>div.CryptoCard-Content>div{
    display: inline-flex;
    margin-right: 1.5rem;
}
#Checkout>div.CryptoCard-Content>div:last-child{
    margin-right: calc(100vw - 2.5rem - 19.2rem);
}
@media all and (min-width: 1024px){
    #Checkout>div.CryptoCard-Content{
        padding-bottom: 1rem;
    }
    #Checkout>div.CryptoCard-Content>div:last-child{
        margin-right: calc(85vw - 20.2rem);                                 /* 19.2 + 1 = 20.2 since there is a scrollbar exists on the right */
    }
}
#Checkout>p.Rate{
    text-align: left;
    color: var(--color-white-normal);
    font-size: var(--font-size-smaller);
    margin-bottom: 1.5rem;
}
#Checkout>button{
    width: 19.2rem;
    font-size: var(--font-size-smaller);
}
#Checkout>button:last-child{
    margin-top: 1rem;
    background-color: var(--color-blue-light);
    border-color: var(--color-blue-light);
}
#Checkout>p.Error{
    display: flex;
    align-items: center;
    height: 1.5rem;
    -webkit-transition: 0.75s;
    transition: 0.75s;
    opacity: 0;
    text-align: left;
    margin-bottom: 0.25rem;
    font-size: var(--font-size-smaller);
    color: var(--color-white-normal);
}
#Checkout>p.Error>i{
    margin-right: 0.25rem;
    color: var(--color-red-normal);
    font-size: var(--font-size-large);
}
#Checkout>p.Error.Actived{
    opacity: 1;
}
/* Suppose all cards' initial size is 480*300 */

img.GiftCard-Simple{
    width: auto;
    height: 100%;
    margin: 0 0.75rem;
}
img.GiftCard-Simple:hover{
    cursor: pointer;
}

div.CryptoCard{
    height: 12rem;                                              /* 8 : 5 */
    width: 19.2rem;                                           
    padding: 1.25rem 1.25rem 2rem 1.25rem;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    border-radius: 0.25rem;
}
div.CryptoCard>div:first-child{
    display: flex;
    align-self: flex-end;
    align-items: center;
    justify-content: flex-end;
}
div.CryptoCard>div:first-child>img{
    height: 1.5rem;
    margin-right: 0.25rem;
}
div.CryptoCard>div:last-child{
    align-self: flex-start;
}
div.CryptoCard>div:last-child>p{
    text-align: left;
}
div.CryptoCard.Bitcoin{
    background: -webkit-linear-gradient(212deg, var(--color-grey-dark), var(--color-black-dark));
    background: linear-gradient(238deg, var(--color-grey-dark), var(--color-black-dark));
}
div.CryptoCard.Bitcoin>div:first-child>p{
    font-weight: 600;
    font-size: var(--font-size-larger);
    color: var(--color-white-normal);
    font-style: italic;
}
div.CryptoCard.Bitcoin>div:last-child>p:first-child{
    font-size: var(--font-size-small);
    color: var(--color-white-darkest);
}
div.CryptoCard.Bitcoin>div:last-child>p:last-child{
    font-size: var(--font-size-larger);
    color: var(--color-white-normal);
}
div.CryptoCard.Ethereum{
    background: -webkit-linear-gradient(212deg, var(--color-white-normal), var(--color-white-dark));
    background: linear-gradient(238deg, var(--color-white-normal), var(--color-white-dark));
}
div.CryptoCard.Ethereum>div:first-child>p{
    font-size: var(--font-size-larger);
    letter-spacing: 0.1rem;
}
div.CryptoCard.Ethereum>div:last-child>p:first-child{
    font-size: var(--font-size-small);
    color: var(--color-grey-dark);
}
div.CryptoCard.Ethereum>div:last-child>p:last-child{
    font-size: var(--font-size-larger);
}
div.CryptoCard.Litecoin{
    background: -webkit-linear-gradient(212deg, var(--color-egg-normal), rgb(85, 71, 64));
    background: linear-gradient(238deg, var(--color-egg-normal), rgb(85, 71, 64));
}
div.CryptoCard.Litecoin>div:first-child>p{
    font-weight: 600;
    font-size: var(--font-size-larger);
    color: rgb(85, 71, 64);
    font-style: italic;
}
div.CryptoCard.Litecoin>div:last-child>p:first-child{
    font-size: var(--font-size-small);
    color: var(--color-white-normal);
}
div.CryptoCard.Litecoin>div:last-child>p:last-child{
    font-size: var(--font-size-larger);
    color: var(--color-white-normal);
}

